@charset "UTF-8";
/* Указываем box sizing */
*, *::before, *::after { box-sizing: border-box; }

/* Убираем внутренние отступы */
ul[class], ol[class] { padding: 0; }

/* Убираем внешние отступы */
body, h1, h2, h3, h4, p, ul[class], ol[class], li, figure, figcaption, blockquote, dl, dd { margin: 0; }

/* Выставляем основные настройки по-умолчанию для body */
body { min-height: 100vh; scroll-behavior: smooth; text-rendering: optimizeSpeed; line-height: 1.5; }

/* Удаляем стандартную стилизацию для всех ul и il, у которых есть атрибут class*/
ul[class], ol[class] { list-style: none; }

/* Элементы a, у которых нет класса, сбрасываем до дефолтных стилей */
a:not([class]) { text-decoration-skip-ink: auto; }

/* Упрощаем работу с изображениями */
img { max-width: 100%; display: block; }

/* Указываем понятную периодичность в потоке данных у article*/
article > * + * { margin-top: 1em; }

/* Наследуем шрифты для инпутов и кнопок */
input, button, textarea, select { font: inherit; }

/* Удаляем все анимации и переходы для людей, которые предпочитай их не использовать */
@media (prefers-reduced-motion: reduce) { * { animation-duration: 0.01ms !important; animation-iteration-count: 1 !important; transition-duration: 0.01ms !important; scroll-behavior: auto !important; } }

/* Включаем выделение текста в slick слайдере */
/* Для выделения текста */
.header__slider, .minib__slider, .portfolio__slider { -webkit-user-select: text; -moz-user-select: text; -ms-user-select: text; user-select: text; }

.header__slider.draggable, .minib__slider.draggable, .portfolio__slider.draggable { -webkit-user-select: none; -moz-user-select: none; -ms-user-select: none; user-select: none; }

/* Убираем outline */
.header__slider .header__item, .minib__slider .minib__elem, .portfolio__slider .portfolio__item { outline: none; }

